.voucher_intro {
    // margin-top: 100px;
    // margin-bottom: 20px;
      margin-top: 20px;
    margin-bottom: 20px;

    @media (min-width:$screen-md) {
    //  margin-top: 150px;
    // margin-bottom: 40px;
    margin-top: 20px;
    margin-bottom: 20px;
    }

    h1 {
        font-family: 'Bebas Neue';
        font-weight: bold;
        // font-size: 72px;
        // line-height: 65px;
         font-size: 50px;
        line-height: 55px;
        letter-spacing: 0.005em;
        text-align: center;
        display: block;
        margin-bottom: 0;

        @media (min-width:$screen-md) {
            // font-size: 149px;
            // line-height: 149px;
              font-size: 110px;
            line-height: 110px;
            text-align: left;
            letter-spacing: 0.015em;
        }

    }

    p {
        font-family: 'Circular Std';
